import Vue from 'vue';
import Router from 'vue-router';
import Home from "./views/Home.vue";
import Organisation from "./views/Organisation.vue";
import Guidelines from "./views/Guidelines.vue";
import Viewer from "./views/Viewer.vue";
Vue.use(Router);
export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior: function scrollBehavior(to) {
    if (to.hash) {
      return {
        selector: to.hash
      };
    } else {
      return {
        x: 0,
        y: 0
      };
    }
  },
  routes: [{
    path: '/',
    name: 'root',
    component: Home
  }, {
    path: '/:org',
    component: Organisation,
    children: [{
      path: '',
      name: 'home',
      component: Home
    }, {
      path: ':cat',
      name: 'category',
      component: Guidelines
    }, {
      path: 'view',
      name: 'viewer',
      component: Viewer,
      props: true
    }]
  }]
});